import React, { useState, useContext, useEffect } from 'react';
import { Button, TableContainer, Paper, Table, TableBody, TableRow, TableCell, TableHead, ClickAwayListener, Tooltip, IconButton } from '@material-ui/core';
import UserContext from '../../../hooks/userContext';
import { Link, useHistory } from 'react-router-dom';
import InfoIcon from '@material-ui/icons/Info';
import useMusicKitAuthentication from '../../../hooks/useMusicKit/useMusicKitAuthentication';
import InformationContext from '../../../hooks/informationContext';
import useSpotifyAuthentication from '../../../hooks/useSpotify/useSpotifyAuthentication';

const UserMeLayout = () => {
  const userContext = useContext(UserContext)
  const infoContext = useContext(InformationContext)
  let history = useHistory()

  const [openInfoPublicInformation, setOpenInfoPublicInformation] = useState(false)
  const [openInfoMusicServiceLogin, setOpenInfoMusicServiceLogin] = useState(false)

  const publicTypes = userContext.state.user?.publicInformations?.map(p=>p.publicType) || []
  const publicArtist = publicTypes.includes("artist") ? "" : ""
  const publicAlbum  = publicTypes.includes("album") ? "" : ""

  const apple   = useMusicKitAuthentication()
  const spotify = useSpotifyAuthentication()

  // 
  useEffect(() => {
    if(userContext.state.user?.registered === false) {
      history.push("/signup")
      infoContext.dispatch({ type: "ADD_ALERT", severity: "info", duration: 20000, text: "", buttonText: "OK" })
    }
  }, [userContext, infoContext, history])

  const canLoginToApple = userContext.state.user?.role.allowedActions.includes("appleMusicToken")
  const appleLoginButton = apple.isAuthorized ?
    <Button disabled={!canLoginToApple} onClick={() => { apple.authentication.logout(); window.location.reload() }} variant="contained"></Button> :
    <Button disabled={!canLoginToApple} onClick={() => apple.authentication.login()} variant="contained"></Button>

  const canLoginToSpotify = userContext.state.user?.role.allowedActions.includes("spotifyToken")
  const spotifyLoginButton = spotify.isAuthorized ?
    <Button disabled={!canLoginToSpotify} onClick={() => { spotify.authentication.logout(); window.location.reload() }} variant="contained"></Button> :
    <Button disabled={!canLoginToSpotify} onClick={() => spotify.authentication.login()} variant="contained"></Button>

  return (
    <TableContainer component={Paper}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell colSpan={2} align="center" style={{ border: 'none' }}></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow >
            <TableCell align="right" style={{ border: 'none' }}></TableCell>
            <TableCell align="left" style={{ border: 'none' }}>{userContext.state.user?.name}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell align="right" style={{ border: 'none' }}>ID</TableCell>
            <TableCell align="left" style={{ border: 'none' }}>{userContext.state.user?.username}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell align="right"></TableCell>
            <TableCell align="left">{userContext.state.user?.role.description}</TableCell>
          </TableRow>
        </TableBody>
      </Table>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell colSpan={2} align="center" style={{ border: 'none' }}>
              
              <ClickAwayListener onClickAway={()=>setOpenInfoPublicInformation(false)}>
                <Tooltip
                  PopperProps={{
                    disablePortal: true,
                  }}
                  onClose={()=>setOpenInfoPublicInformation(false)}
                  open={openInfoPublicInformation}
                  disableFocusListener
                  disableHoverListener
                  disableTouchListener
                  placement="top"
                  title=""
                >
                  <IconButton size="small" onClick={()=>setOpenInfoPublicInformation(true)}>
                    <InfoIcon fontSize="small" />
                  </IconButton>
                </Tooltip>
              </ClickAwayListener>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow>
            <TableCell align="right" style={{ border: 'none' }}></TableCell>
            <TableCell align="left" style={{ border: 'none' }}>{publicArtist}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell align="right"></TableCell>
            <TableCell align="left">{publicAlbum}</TableCell>
          </TableRow>
        </TableBody>
      </Table>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell colSpan={2} align="center" style={{ border: 'none' }}>
              
              <ClickAwayListener onClickAway={()=>setOpenInfoMusicServiceLogin(false)}>
                <Tooltip
                  PopperProps={{
                    disablePortal: true,
                  }}
                  onClose={()=>setOpenInfoMusicServiceLogin(false)}
                  open={openInfoMusicServiceLogin}
                  disableFocusListener
                  disableHoverListener
                  disableTouchListener
                  placement="top"
                  title=""
                >
                  <IconButton size="small" onClick={()=>setOpenInfoMusicServiceLogin(true)}>
                    <InfoIcon fontSize="small" />
                  </IconButton>
                </Tooltip>
              </ClickAwayListener>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow>
            <TableCell align="right" style={{ border: 'none' }}>Apple Music</TableCell>
            <TableCell align="left" style={{ border: 'none' }}>
              {appleLoginButton}
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell align="right">Spotify</TableCell>
            <TableCell align="left">
              {spotifyLoginButton}
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell colSpan={2} align="center">
              <Button component={Link} to={"/me/edit"} variant="contained"></Button>
            </TableCell>
          </TableRow>
        </TableHead>
      </Table>
    </TableContainer>
  )
}

export default UserMeLayout
